'use client'

import { useState, useEffect, useCallback, useRef } from 'react'
import { Button } from '@/components/ui/button'
import { useMobile } from '@/hooks/use-mobile'
import '@/styles/game.css'

type GameState = 'START' | 'PLAYING' | 'PAUSED' | 'GAME_OVER'

interface Bubble {
  id: number
  x: number
  y: number
  size: number
  speed: number
}

export default function GameBoard() {
  const initialState = {
    gameState: 'START' as GameState,
    score: 0,
    timeLeft: 60,
    bubbles: [] as Bubble[],
    highScore: 0
  }

  const [gameState, setGameState] = useState(initialState.gameState)
  const [score, setScore] = useState(initialState.score)
  const [timeLeft, setTimeLeft] = useState(initialState.timeLeft)
  const [bubbles, setBubbles] = useState(initialState.bubbles)
  const [highScore, setHighScore] = useState(initialState.highScore)
  const gameRef = useRef<HTMLDivElement | null>(null)
  const timerRef = useRef<NodeJS.Timeout | undefined>(undefined)
  const bubbleIntervalRef = useRef<NodeJS.Timeout | undefined>(undefined)
  const animationFrameRef = useRef<number | undefined>(undefined)
  const isMobile = useMobile(768)

  // Keep track of the last used ID
  const lastIdRef = useRef(0)

  const createBubble = useCallback((): Bubble => {
    const gameArea = gameRef.current?.getBoundingClientRect()
    lastIdRef.current += 1 // Increment ID counter
    
    if (!gameArea) return { 
      id: lastIdRef.current,
      x: 50,
      y: 50,
      size: 50,
      speed: 2
    }

    const size = Math.random() * 30 + 40 // Bubble size between 40-70px
    const x = Math.random() * (gameArea.width - size)
    const y = gameArea.height // Start from bottom
    const speed = Math.random() * 2 + 1 // Speed between 1-3

    return {
      id: lastIdRef.current,
      x,
      y,
      size,
      speed,
    }
  }, [])

  const startGame = useCallback(() => {
    setGameState('PLAYING')
    setScore(0)
    setTimeLeft(60)
    setBubbles([])
    
    // Start timer
    if (timerRef.current) clearInterval(timerRef.current)
    timerRef.current = setInterval(() => {
      setTimeLeft((prev) => {
        if (prev <= 1) {
          clearInterval(timerRef.current)
          setGameState('GAME_OVER')
          return 0
        }
        return prev - 1
      })
    }, 1000)

    // Start creating bubbles
    if (bubbleIntervalRef.current) clearInterval(bubbleIntervalRef.current)
    
    // Create initial bubbles
    setBubbles([createBubble(), createBubble(), createBubble()])
    
    // Continue creating bubbles
    bubbleIntervalRef.current = setInterval(() => {
      setBubbles((prev) => {
        // Limit maximum bubbles to prevent overwhelming the screen
        if (prev.length >= 10) {
          return [...prev.slice(1), createBubble()]
        }
        return [...prev, createBubble()]
      })
    }, 800) // Faster interval for more engaging gameplay
  }, [createBubble])

  const endGame = useCallback(() => {
    clearInterval(timerRef.current)
    clearInterval(bubbleIntervalRef.current)
    setGameState('GAME_OVER')
    setHighScore((prev) => Math.max(prev, score))
  }, [score])

  const handleBubblePop = useCallback(async (id: number) => {
    setBubbles((prev) => prev.filter((bubble) => bubble.id !== id))
    setScore((prev) => prev + 1)
    
    // Play pop sound using Web Audio API
    try {
      const audioContext = new (window.AudioContext || (window as any).webkitAudioContext)()
      const oscillator = audioContext.createOscillator()
      const gainNode = audioContext.createGain()
      
      oscillator.connect(gainNode)
      gainNode.connect(audioContext.destination)
      
      // Configure sound
      oscillator.type = 'sine'
      oscillator.frequency.setValueAtTime(800, audioContext.currentTime)
      oscillator.frequency.exponentialRampToValueAtTime(100, audioContext.currentTime + 0.1)
      
      gainNode.gain.setValueAtTime(0.3, audioContext.currentTime)
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.1)
      
      // Play and stop
      oscillator.start()
      oscillator.stop(audioContext.currentTime + 0.1)
    } catch (error) {
      console.error('Failed to play sound:', error)
    }
  }, [])

  useEffect(() => {
    return () => {
      clearInterval(timerRef.current)
      clearInterval(bubbleIntervalRef.current)
    }
  }, [])

  // Update bubbles position
  useEffect(() => {
    if (gameState !== 'PLAYING') return

    const updateBubbles = () => {
      setBubbles((prev) =>
        prev
          .map((bubble) => ({
            ...bubble,
            y: bubble.y - bubble.speed,
          }))
          .filter((bubble) => bubble.y + bubble.size > 0)
      )
      animationFrameRef.current = requestAnimationFrame(updateBubbles)
    }

    animationFrameRef.current = requestAnimationFrame(updateBubbles)
    return () => {
      if (animationFrameRef.current) {
        cancelAnimationFrame(animationFrameRef.current)
      }
    }
  }, [gameState])

  return (
    <div className="relative w-full h-screen overflow-hidden game-background" ref={gameRef}>
      {/* Game UI */}
      {gameState === 'PLAYING' && (
        <div className="absolute top-4 left-4 right-4 flex justify-between items-center z-10">
          <div className="text-2xl font-bold text-white game-score">Score: {score}</div>
          <div className="text-2xl font-bold text-white game-timer">Time: {timeLeft}s</div>
        </div>
      )}

      {/* Bubbles */}
      {gameState === 'PLAYING' &&
        bubbles.map((bubble) => (
          <button
            key={bubble.id}
            className="bubble absolute rounded-full cursor-pointer transform transition-all duration-200
                     animate-float hover:scale-110 active:scale-90 focus:outline-none"
            style={{
              left: `${bubble.x}px`,
              top: `${bubble.y}px`,
              width: `${bubble.size}px`,
              height: `${bubble.size}px`,
              background: 'radial-gradient(circle at 30% 30%, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.4))',
              boxShadow: '0 0 10px rgba(255, 255, 255, 0.5), inset 0 0 20px rgba(255, 255, 255, 0.5)',
              border: '1px solid rgba(255, 255, 255, 0.3)',
              animation: `float ${2 + Math.random()}s ease-in-out infinite`
            }}
            onClick={() => handleBubblePop(bubble.id)}
          />
        ))}

      {/* Background gradient */}
      <div 
        className="fixed inset-0 -z-10 bg-gradient-to-br from-blue-400 via-purple-400 to-pink-400 opacity-80"
        style={{ filter: 'blur(100px)' }}
      />

      {/* Start Screen */}
      {gameState === 'START' && (
        <div className="absolute inset-0 flex flex-col items-center justify-center game-overlay">
          <div className="bg-white/90 rounded-lg p-8 text-center max-w-md mx-4 shadow-xl">
            <h1 className="text-4xl font-bold mb-4 bg-gradient-to-r from-blue-500 to-purple-500 bg-clip-text text-transparent">
              Bubble Pop Adventure
            </h1>
            <p className="mb-6 text-gray-600">
              Pop as many bubbles as you can in 60 seconds!
            </p>
            <Button 
              onClick={startGame}
              className="w-full bg-gradient-to-r from-blue-500 to-purple-500 text-white hover:opacity-90 transition-opacity"
            >
              Start Game
            </Button>
          </div>
        </div>
      )}

      {/* Game Over Screen */}
      {gameState === 'GAME_OVER' && (
        <div className="absolute inset-0 flex flex-col items-center justify-center game-overlay">
          <div className="bg-white/90 rounded-lg p-8 text-center max-w-md mx-4 shadow-xl">
            <h2 className="text-3xl font-bold mb-4 text-gray-800">Game Over!</h2>
            <p className="text-xl mb-2 text-gray-600">Final Score: {score}</p>
            <p className="text-lg mb-6 text-gray-500">High Score: {highScore}</p>
            <Button 
              onClick={startGame}
              className="w-full bg-gradient-to-r from-blue-500 to-purple-500 text-white hover:opacity-90 transition-opacity"
            >
              Play Again
            </Button>
          </div>
        </div>
      )}
    </div>
  )
}
